<template>
  <div class="all">
    <!-- 加载主题列表 -->
    <topic-list :items="items"></topic-list>
    <infinite-loading   v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>
// 引入 取数据的模块
import {getTopics} from '@/getdata/getTopic.js';
// 引入 主题列表组件
import topicList from '@/components/topics/List';
// 引入 分页滑动组件
import InfiniteLoading from 'vue-infinite-loading';


export default {
  name: 'All',
  data () {
    return {
      // 主题表
      items:[],
      page:1,
      isShow:true
      
    }
  },
  
  mounted:function(){
  	getTopics({tab:'all',limit:10,page:this.page}).then((response)=>{
      // console.log(response);
      this.items = response.data.data;
    });
  },

  methods: {
    infiniteHandler($state) {
      setTimeout(() => {
        // 查询 全部主题数据
        getTopics({tab:'all',limit:20,page:++this.page}).then((response)=>{
          // console.log(response.data.data);
          // 把查询到的数据放到 items里
          this.items = this.items.concat(response.data.data);
          if(response.data.data.length ==0){
            this.isShow=false;
            return;
          }
          $state.loaded();
        });
      }, 500);
    },
  },
  // 加载组件
  components:{
    topicList,InfiniteLoading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

</style>



