<template>
  <div class="list">
     <!-- loading 组件 -->
    <loading v-if="!items.length"></loading>
    <!-- 主题列表 -->
    <ul class="items">
      <li class="item" v-for="item in items">
        <img v-lazy="item.author.avatar_url" alt="">
        <div class="wrap-box">
          <div class="wrap-t">
            <div class="tab">
            <div class="mo" v-if="item.top">置顶</div>
            <div class="mo" v-else-if="item.good">精华</div>
            <div v-else>{{tabs[item.tab]}}</div>
            </div>
            <div class="title1">
              <div class="text">
               <router-link :to=" '/index/show/'+item.id" tag="div" class="title">
                 {{item.title}}
              </router-link>
              </div>
            </div>
          </div>
          <div class="wrap-b">
            <div class="number">{{item.reply_count}}/{{item.visit_count}}</div>
            <div class="null"></div>
            <div class="time">{{setTime(item.last_reply_at)}}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
//引入时间timeago
import timeago from 'timeago.js';
// 引入 Loading 组件
import loading from '@/components/Loading'; 
export default {
  name: 'List',
  data () {
    return {
     // 主题分类
      tabs:{
        weex:'Weex',
        ask:'问答',
        job:'招牌',
        share:'分享'
      }
    }
  },

  methods:{
    setTime(time){
      var timeagoInstance = timeago();
      return timeagoInstance.format(time, 'zh_CN');
    }
  },

  props:['items'],
  // 加载组件
  components:{
    // loading 组件
    loading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入公共样式
  @import '../../assets/sass/base.scss';
  // 引入itemlist样式
  @import '../../assets/sass/itemlist.scss';

  .list{
    margin-top: rem(280px);
    margin-bottom: rem(140px);
  }

</style>



