<template>
  <div class="weex">
    <!-- 主题列表 -->
    <topic-list :items="items"></topic-list> 
     <infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading> 
  </div>
</template>

<script>
// 引入 主题列表组件
import topicList from '@/components/topics/List'; 
// 引入 取数据的模块
import {getTopics} from '@/getdata/getTopic.js';
// 引入 分页滑动组件
import InfiniteLoading from 'vue-infinite-loading';

export default {
  name: 'Weex',
  data () {
    return {
      // 主题列表
      items:[],
      page:1,
      isShow:true    
    }
  },
  // 钩子函数
  mounted: function () {
    // 查询 weex 数据
    getTopics({tab:'weex',limit:10,page:this.page}).then((response)=>{
       console.log(response);
      // 把查询的数据放到 items 
      this.items = response.data.data;
    });   
  },

  methods: {
    infiniteHandler($state) {
      setTimeout(() => {
        // 查询 全部主题数据
        getTopics({tab:'week',limit:5,page:++this.page}).then((response)=>{
          console.log(response.data.data);
          // 把查询到的数据放到 items里
          this.items = this.items.concat(response.data.data);

          if(response.data.data.length==0){
            this.isShow=false;
            return;
          }
          $state.loaded();
        });
      }, 500);
    },
  },
  // 加载组件
  components:{
    // 主题列表
    topicList,InfiniteLoading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



