 <template>
  <div class="show">
    <!-- loading 组件 -->
    <!-- <loading v-if="!items.length"></loading> -->
    <ul class="items">
      <li class="item">
        <div class="top">
            <div class="title">
              {{title}}
            </div>
            <div class="aren">
              <span>发布时间：{{setTime(last_reply_at)}}</span>
              <span>作者：{{author}}</span>
              <span>浏览次数：{{visit_count}}</span>
              <!-- <span class="where">来自:
                  <span v-if="data.top">置顶</span>
                  <span v-else-if="data.good">精华</span>
                  <span v-else>{{tabs[data.tab]}}</span>
              </span> -->
            </div>
            <div class="content" v-html="content"></div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  // 引入 Loading 组件
import loading from '@/components/Loading'; 
  //引入时间timeago
import timeago from 'timeago.js';
// 引入 取数据的模块
import {getTopicData} from '@/getdata/getTopic.js';

export default {
  name: 'Show',
  data () {
    return {
      // data:'',
      title:'',
      author:'',
      loginname:'',
      content:'',
      visit_count:'',
      last_reply_at:'',
    }
  },
  // 方法
  mounted:function(){
    // 文章的id
    var id = this.$route.params.id;
    getTopicData(id).then((response)=>{
      console.log(response);
       this.title = response.data.data.title;
       this.author = response.data.data.author.loginname;
       this.visit_count = response.data.data.visit_count;
       this.last_reply_at = response.data.data.last_reply_at;
       this.content = response.data.data.content;
       this.data = response.data.data;
       // console.log(this.items);
    })
  },
   methods:{
    setTime(time){
      var timeagoInstance = timeago();
      return timeagoInstance.format(time, 'zh_CN');
    }
  },


  // props:['data'],
  // 加载组件
  components:{
    // loading 组件
    // loading
  }
}
</script>

<style lang="scss">
  // 引入 公共样式
  @import '../../assets/sass/base.scss';
  .show{
    width:100%;
    // background:#e1e1e1;
    background:#fff;
    margin-top: rem(300px);
    margin-bottom: rem(120px);
    width: 100%;
    .top{
      width:rem(710px);
      margin:0 auto;
      background:#fff;
      .title{
        white-space: wrap;
        font-size: rem(50px);
        font-weight: bold;
        width:100%;
        // background:red;
        line-height: rem(90px);
        text-align: center;
        font-size:rem(50px);
      }
      .aren{
        width:100%;
        height:rem(60px);
        display: flex;
        align-items: center;
        justify-content:space-around;
        font-size: rem(25px);
      }
      .content{
        img{
          width:100%;
        }
        h2{
          width:100%;
          height: rem(50px);
          text-align: center;
          line-height: auto;
        }
        h1{
          width:100%;
          height: rem(50px);
          text-align: center;
          line-height: auto;
        }
        p{
          letter-spacing:rem(2px);
          line-height: rem(35px);
          font-size: rem(20px);
          margin: rem(20px) auto;
          width:100%;
          height:auto;
        }
        ul{
          li{
            width:100%;
            white-space: nowrap;
            overflow:hidden;
            line-height: rem(45px);
            text-overflow: ellipsis;
            height:rem(45px);
          }
        }
      }
    }
  }
</style>



