<template>
  <div class="book">
    <div class="toptitle">起点小说</div>
    <ul class="items">
      <li class="item" v-for="item in items">
        <div class="pic">
          <div class="img">
            <img :src="item.book_cover" alt="">
          </div>
          <div class="text">
            <div class="class">类别：{{item.class_name}}</div>
            <div class="zt">状态：{{item.stat_name}}</div>
            <div class="key">关键字:{{item.tag}}</div>
          </div>
        </div>
        <div class="content">
          <div class="title">{{item.bookname}}</div>
          <div class="author">作者：{{item.author_name}}</div>
          <div class="one">{{item.topic_first}}</div>
          <div class="act">{{item.introduction}}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
// 引入 axios 模块（封装的 ajax）
import axios from 'axios';

export default {
  name: 'book',
  data () {
    return {
      // 主题列表
      items:[],
      tag:[],
    }
  },
  // 钩子函数
  mounted: function () {
    // // ES6 
    axios.get('https://www.apiopen.top/novelApi').then((response)=>{
      this.items = response.data.data;
      console.log(this.items);
    }); 
  },
 
}


</script>

<style lang="scss">
  // 引入 公共样式
@import '../assets/sass/base.scss';

.book{
  background: #315f36;
  opacity: .9;
    margin-bottom:rem(120px);
    .toptitle{
      width:100%;
      height:rem(150px);
      line-height: rem(150px);
      font-weight: bold;
      text-align: center;
      font-size: rem(50px);
    }
  .items{
    .item{
      width:rem(710px);
      height:rem(520px);
      background:#fff;
      margin:0 auto rem(10px) auto;
      display: flex;
      .pic{
        background: #ccc;
        opacity: .8;
        height:rem(520px);
        width: rem(250px);
          .img{
            img{
              display: block;
              margin-top: rem(20px);
              vertical-align: middle;
              width: rem(250px);
              height:rem(350px); 
            }
          }
          .text{
            width:100%;
            height:rem(150px);
            // background:red;
            .class{
              text-align: center;
              width:100%;
              height: rem(40px);
              line-height: rem(40px);
            }
            .zt{
              text-align: center;
              width:100%;
              height: rem(40px);
              line-height: rem(40px);
            }
            .key{
              padding:0 rem(10px);
            }
          }
      }
      .content{
        background: #ccc;
        opacity: .6;
        width:rem(500px);
        height:rem(520px);
        padding: 0 rem(13px);
        .title{
          font-size: rem(30px);
          font-weight: bold;
          width:100%;
          height:rem(90px);
          text-align: center;
          line-height: rem(90px);
        }
        .author{
          width:100%;
          font-size: rem(25px);
          height:rem(50px);
          text-align: center;
          line-height: rem(50px);
        }
        .one{
          width:100%;
          height:rem(50px);
          text-align: center;
          line-height: rem(50px);
        }
        .act{
          width:100%;
          height:rem(320px);
          // background: red;
          text-overflow: ellipsis;
          white-space: wrap;
          font-size: rem(24px);
          line-height: rem(40px);
          letter-spacing: rem(2px);
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 8;
          overflow: hidden;
        }
      }
    }
  }
}
  
</style>


