<template>
  <div class="index">
    <!-- loading 组件 -->
    <!-- <loading v-if="!items.length"></loading> -->
  <ul class="items">
      <li class="item" v-for="item in items">
        <div class="city">
          <h1 class="title">{{item.basic.location}}</h1>
        </div>
        <ul class="todywendu" v-for="todywendu in now">
          <li class="tianqi">{{todywendu.now.tmp}}<sup>°C</sup></li>
          <li class="miaoshu"> {{todywendu.now.cond_txt}}</li>
          <li class="xidu">湿度:{{todywendu.now.hum}}</li>
        <li class="fengsu">风速:{{todywendu.now.wind_spd}}/km</li>
        <li class="shidu">风向:{{todywendu.now.wind_dir}}</li>
        <li class="fabu">发布时间:{{todywendu.update.utc}}</li>
        </ul>

        <div class="yubao">
          <span class="jintian">{{item.daily_forecast[0].date}}&nbsp;今天</span>
          <span class="wendu">温度：{{item.daily_forecast[0].tmp_max}}~{{item.daily_forecast[0].tmp_min}}</span>
          <span class="shidu">湿度：{{item.daily_forecast[0].hum}}</span>

        </div>
        <div class="yubao">
          <span class="jintian">{{item.daily_forecast[1].date}}&nbsp;明天</span>
          <span class="wendu">温度：{{item.daily_forecast[1].tmp_max}}~{{item.daily_forecast[0].tmp_min}}</span>
          <span class="shidu">湿度：{{item.daily_forecast[1].hum}}</span>
        </div>
        <div class="yubao">
          <span class="jintian">{{item.daily_forecast[2].date}}&nbsp;今天</span>
          <span class="wendu">温度：{{item.daily_forecast[2].tmp_max}}~{{item.daily_forecast[0].tmp_min}}</span>
          <span class="shidu">湿度：{{item.daily_forecast[2].hum}}</span>
        </div>
      </li> 
    </ul> 
  </div>
</template>

<script>
// 引入 axios 模块（封装的 ajax）
import axios from 'axios';
// 引入 Loading 组件
// import loading from '@/components/Loading';
export default {
  name: 'Index',
  data () {
    return {
       // 主题列表
      items:[],
      now:[],
    }
  },
  // props:['items'],
  // // 加载组件
  // components:{
  //   // loading 组件
  //   loading
  // },
  // 钩子函数
  mounted: function () {
    // ES6 
    axios.get('https://free-api.heweather.com/s6/weather/forecast?location=yangjiang &key=54313a25242d488fb32c78facf62c0de').then((response)=>{
      this.items = response.data.HeWeather6;
      console.log(this.items);
    }); 
    axios.get('https://free-api.heweather.com/s6/weather/now?location=yangjiang &key=54313a25242d488fb32c78facf62c0de').then((response)=>{
      this.now = response.data.HeWeather6;
      console.log(this.now);
    });     
 },
}
</script>
<style scoped lang="scss">
@import '../assets/sass/base.scss';

.index{
  width: 100%;
  height:rem(1300px);
  background:skyblue;
  opacity: .9;
  // background:url(../../static/images/1.jpg) no-repeat;
  // background-size:100% 100%;
  .items{
    width: rem(720px);
    height: rem(1300px);
    margin: 0 auto; 
    // background: rgba(255,255,255,.3);
    .city{
      width: rem(720px);
      // background:red;
      height: rem(95px);
      line-height: rem(95px);

      h1{
        letter-spacing: rem(3px);
        text-align: center;
        color:#fff;
        }
      }
    .todywendu{
      width:100%;
      height: rem(480px);
      color:#fff;
      margin: rem(30px) auto rem(20px) auto;
      font-size: rem(150px);
      text-align: center;
      letter-spacing: rem(2px);
      .tianqi{
        padding-left: rem(10px);
        width: rem(240px);
        height: rem(170px);
        sup{
          font-size: rem(80px);
        }
      }
      .miaoshu{
        margin-top: (10px);
        width: rem(240px);
        line-height: rem(100px);
        height: rem(100px);
        text-align: center;
        font-size: rem(35px);
      }
      .xidu{
        height: rem(45px);
        line-height: rem(45px); 
        font-size: rem(25px);
      }
      .fengsu{
        height: rem(45px);
        line-height: rem(45px); 
        font-size: rem(25px);
      }
      .shidu{
        height: rem(45px);
        line-height: rem(45px); 
        font-size: rem(25px);
      }
      .fabu{
        margin-top: rem(20px);
        float:right;
        height: rem(30px);
        line-height: rem(30px); 
        font-size: rem(16px);
      }
    }
    .yubao{
      display: flex;
      align-items: center;
      justify-content: space-around;
      width:100%;
      height:rem(200px);
      border-top: rem(1px) solid rgba(28,28,28,.3);
      border-bottom: rem(1px) solid rgba(28,28,28,.3);

      span{
        color:#fff;
        font-size: rem(30px);
      }
      .jintian{
        padding-right:rem(10px);
      }
    } 
  }
}
</style>


