<template>
  <div class="index">
    <div class="Toparen">
      <div class="wrap">
         <div class="logo-wrap">
           <div class="logo"></div>
           <div class="text">Vue.js</div>
         </div>
         <div class="form">
          <form action="">
           <div>
             <input type="text">
           </div>
          </form>
          </div>
      </div>
      <nav class="top">
        <ul>
          <router-link to="/index/" tag="li" class="mo">全部</router-link>
            <router-link to="/index/good" tag="li">精华</router-link>
            <router-link to="/index/weex" tag="li">weex</router-link>
            <router-link to="/index/share" tag="li">分享</router-link>
            <router-link to="/index/ask" tag="li">问答</router-link>
            <router-link to="/index/job" tag="li">招聘</router-link>
        </ul>
      </nav>   
    </div>
  	
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	@import '../assets/sass/base.scss';
  .Toparen{
    position:fixed;
    top:0;
    left:0;
    width: 100%;
    height: rem(280px);
    background: #315f36;
    .wrap{
      width:100%;
      height: rem(190px);
      .logo-wrap{
        width: rem(200px);
        height: rem(110px);
        margin:0 auto;
        display: flex;
        align-items: center;
        justify-content: space-around;
        .logo{
          width: rem(90px);
          height: rem(90px); 
          background: url('../assets/logo.png') no-repeat;
          background-size: rem(90px) rem(90px);
        }
        .text{
          // padding-right: rem(20px);
          width: rem(90px);
          height: rem(90px);
          text-align: center;
          line-height: rem(90px);
          color:#fff;
          font-size: rem(40px);
          font-weight: bold;
        }
       
      }

       .form{
        width: rem(500px);
        height: rem(60px);
            margin:0 auto;
          // margin-left: rem(10px);
          form{
            div{
              width: rem(500px);
              height: rem(50px);
              background:#4f9639;
              border-radius: rem(20px);
              input{
              width: rem(500px);
              height: rem(50px);
              border-radius: rem(20px);
              outline: none;
              border: none;
              background:url(https://www.vue-js.com/public/images/search.png) no-repeat 4px 4px;
              padding: 3px 5px 3px 22px;
              color:#ccc;
              

              }
            }
           
          }
        }  
    }
      // 顶部导航
    nav.top{
      position: fixed;
      top: rem(190px);
      left:0;
      width:100%;
      height: rem(90px);
      background: #f6f6f6;
      border:none;
      ul{
        display: flex;
        align-items: center;
        justify-content: space-around;
        height: rem(90px);
        li{
          font-size: rem(40px);
          font-family: '楷体';
          padding: rem(8px) rem(18px);
          border-radius: rem(10px);
          font-weight: bold;
          color:#509030;
          &.router-link-exact-active{
            color:#fff;
            background: #509030;
          }
        }
      }
    }

  }
	
</style>



